<template>
  <div class="user" v-if="userArr">
    <base-panel class="base-panel">
      <template slot="panel-head">
        <router-link to="/">/主页</router-link>
      </template>
      <template slot="panel-content">
        <div class="user-header">
          <div>
            <img :src="userArr.avatar_url" alt="" />
            <span class="text">{{ loginname }}</span>
          </div>
          <span>{{ userArr.score }} 积分</span>
          <span>注册事件{{ userArr.create_at | fromNow }}</span>
        </div>
      </template>
    </base-panel>
    <base-panel class="base-panel">
      <template slot="panel-head"> 最近创建的话题 </template>
      <template slot="panel-content">
        <topics-item
          v-for="item in userArr.recent_topics"
          :key="item.id"
          v-bind="item"
          isUser="true"
        />
      </template>
    </base-panel>
    <base-panel class="base-panel">
      <template slot="panel-head"> 最近添加的话题 </template>
      <template slot="panel-content">
        <topics-item
          v-for="item in userArr.recent_replies"
          :key="item.id"
          v-bind="item"
          isUser="true"
        />
      </template>
    </base-panel>
  </div>
</template>

<script>
import { getUserInfo } from "../plugins/api";
import basePanel from "../components/layout/basePanel.vue";
import TopicsItem from "../components/TopicsItem.vue";
export default {
  components: { basePanel, TopicsItem },
  props: ["loginname"],
  data() {
    return {
      userArr: null,
    };
  },
  // async created() {
  //   const res = await getUserInfo(this.loginname);
  //   this.userArr = res.data;
  //   console.log(this.userArr);
  // },
  watch: {
    loginname: {
      async handler(val) {
        const res = await getUserInfo(this.loginname);
        this.userArr = res.data;
        console.log(this.userArr);
      },
      immediate: true,
    },
  },
};
</script>

<style lang="less" scoped>
.base-panel {
  margin-bottom: 10px;
  margin-top: 15px;
  .user-header {
    display: flex;
    flex-direction: column;
    img {
      width: 40px;
      margin: 10px;
    }
    .text {
      display: inline-block;
      // margin-bottom: 10px;
    }
  }
}
</style>
